import React from 'react'
import { ReactComponent as Checked} from './../../image/sofa/check-line.svg'
import { useState } from 'react'

function CatalogColorButtonLight() {

	const [btnState, setBtnState] = useState(false);

	function handleClick() {
		setBtnState(btnState => !btnState);
		console.log('clicked')
	}

	let toggleClassCheck = btnState ? 'checked' : '';

  return (
	 <>
		<div className={`column-catalog__elipse card-koleksi__elipse ${toggleClassCheck}`} onClick={handleClick}>
			<span></span>
			<span></span>
			<Checked className='svg-checked' />
		</div>
	 </>
  )
}

export default CatalogColorButtonLight
